// main
html, body {
  overflow-y: visible;
}

*, *:before, *:after {
     -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
$bg-color: #fefefe;

body {
  background-color: $bg-color;
  color: #666666;
  font-family: 'Helvetica', 'Calibri', sans-serif;
  width: 100%;
  padding: 0;
  margin: 0;
  position: relative;
}
h1, h2 {
  margin: 0;
}



.content {
  padding: $space-s;
  max-width: 666px;
  margin: 0 auto;
  position: relative;
}

a {
  color: #666666;
}

.ball {
  display: block;
  border: solid 1px #666;
  width:  30px;
  height: 30px;
  border-radius: 100%;
  position: absolute;
  left: $space-m;
  bottom: 0;
  transform: translateY(50%);
  background-color: $bg-color;
}




.divider {
  border-bottom: solid 1px #666;
  width: 100%;
  display: block;
  position: relative; 
  margin: $space-m auto;

  &:after, &:before {
    content: '';
    @extend .ball;
    left: 50%;
    transform: translate(-50%, 50%);
    width: 20px;
    height: 20px;
  }

  &:before {
    border: solid 10px $bg-color;
    width: 60px;
  }
}

.mobile-only {
  @include mq(501px) {
    display: none;
  }
}

.tablet-only {
  @include mq_small(500px) {
    display: none;
  }

  @include mq(1000px) {
    display: none;
  }  
}

.desktop-only {
  @include mq_small(1000px) {
    display: none;
  }
}

.line {
  width: 100%;
  display: block;
  border-bottom: solid 1px #666;
}